@import "../../../assets/styles/mixin.scss";
@import "../../../assets/styles/variable.scss";
.com-card {
  position: relative;
  background-color: #f2f2f2;
  border: 1px solid #97979718;
  padding: 1.5rem;
  line-height: 1.5;
  height: 100%;
  margin-left: $pdMobile;
  transition: 0.4s all ease-in-out;

  img {
    // max-height: 75px;
    margin-bottom: 1.5rem;
    width: unset;
  }
  .title {
    font-weight: 800;
    font-size: 1.75rem;
    line-height: 1.25;
    text-transform: capitalize;

    &:nth-child(odd) {
      height: 2.5rem;
      @include desktop2k() {
        height: 3.5rem;
      }
    }
  }
  .content {
    padding-bottom: 2rem;
    @include desktop2k() {
      padding-bottom: 4rem;
    }
    &.large {
      padding-bottom: 4rem;
    }
  }
  .action {
    display: flex;
    align-items: center;
    font-weight: 600;
    margin-top: 0.5rem;
    position: absolute;
    bottom: 1rem;
    user-select: none;
    cursor: pointer;
    & > svg {
      margin-right: 5px;
      @include desktop2k() {
        font-size: 2rem !important;
      }
    }
    @include desktop2k() {
      font-size: 1.5rem;
    }
  }
  ul {
    list-style-type: disc;
    padding-left: 1rem;
  }
}
@include desktop {
  .com-offerList2 {
    .com-card {
      .title {
        height: 2.5rem;
        @include desktop2k() {
          height: 3.5rem;
        }
      }
    }
  }
  .com-card {
    padding: 2.25rem;
    margin-left: unset;
    @include desktop2k {
      padding: 1.5rem;
    }
    @include desktop4k {
      padding: 2.5rem;
    }
    img {
      max-height: 85px;
      min-height: 85px;
      margin-bottom: 2rem;
      width: unset !important;
      @include desktop2k {
        max-height: unset;
        height: 200px;
      }
    }
    .title {
      font-size: 1.5rem;
      text-transform: capitalize;
      // height: 30px;
      @include desktop2k {
        font-size: 2rem !important;
        height: 3.5rem !important;
      }
    }
    .content {
      font-size: 1rem;
      @include desktop2k {
        font-size: 1.5rem;
      }
    }
    &:hover {
      transform: translateY(-15px);
      box-shadow: 0 20px 20px rgba(0, 0, 0, 0.07);
    }
  }
}
