@import "../../../assets/styles/mixin.scss";
@import "../../../assets/styles/variable.scss";
.btn-offer-desk {
  @include btnDesk();
  position: absolute;
  left: 0;
  top: 300px;
  display: block;
  margin: auto;
  margin-bottom: 3rem;
  margin-left: 5rem;
  width: fit-content;
  display: none;
}
.icon-slide {
  .slick-slider {
    cursor: url("../../../assets/images/cursor/slider_icon_2.png") 25 15, auto;
  }
}
.btn-offer {
  @include btn();
  display: block;
  margin: $pdMobile;
}
.com-strength {
  position: relative;
  padding: 2rem 0;
  padding-top: 0;
  overflow: hidden;
  .label {
    color: $hong-color;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 700;
    margin: 1rem auto -1rem $pdMobile;
  }
  .wrapper {
    margin: $pdMobile;
    margin-bottom: 3rem;
    .title {
      font-size: 2rem;
      max-width: 80%;
      @include title();
    }
    .description {
      margin-left: 2rem;
    }
  }

  //Over ride custom slider for Strengt
  @include mobile {
    .slick-slider {
      .slick-prev:before,
      .slick-next:before {
        content: "" !important;
      }
      .slick-track {
        display: flex;
      }
      .slick-track .slick-slide {
        display: flex;
        height: auto;
      }
      .slick-arrow {
        top: 0;
        button {
          border: 1.5px solid $hong-color;
          box-shadow: none;
          background-color: $hong-color;
          color: white;
          transition: 0.4s all ease-in;
        }
        &.slick-disabled {
          button {
            background-color: white;
            color: black;
          }
        }
        &.slick-prev {
          left: 2.15rem;
          button {
            span {
              transform: rotate(-180deg);
              border-radius: 50%;
              overflow: hidden;
            }
          }
        }
        &.slick-next {
          left: 7rem;
        }
      }
    }
  }
}

@include desktop {
  .btn-offer-desk {
    display: block;
    @include btn-effect($hong-color);
  }
  .btn-offer {
    display: none;
  }
  .com-strength {
    position: relative;
    display: flex;
    padding-top: 2rem;
    padding-left: $pdDesk;
    padding-bottom: 50px;
    .label {
      position: absolute;
      top: 3rem;
      left: 5rem;
      margin: 0;
    }

    .wrapper {
      width: 20vw;
      max-width: unset;
      margin-top: 4rem;
      margin-left: 0;
      margin-bottom: 1rem;
      @include desktop2k {
        width: 22vw;
      }
      .title {
        font-size: 2.35rem;
        @include desktop2k {
          font-size: 4rem;
        }
      }
      .description {
        margin: 2rem 0;
        font-size: 1.15rem;
      }
    }

    //Over ride custom slider for Strengt
    .slick-slider {
      padding-top: 50px;
      width: 65vw;

      .slick-arrow {
        bottom: 25%;
        top: unset;
        @include desktop2k() {
          transform: scale(1.6);
        }
        &.slick-prev {
          left: -20vw;
          @include desktop2k() {
            left: -19vw;
          }
        }
        &.slick-next {
          left: -16vw;
        }
      }
      .slick-track .slick-slide {
        padding: 1rem;
        @include desktop2k {
          padding: 1rem;
        }
        @include desktop4k {
          padding: 2rem;
        }
      }
    }
  }
}
