@import "assets/styles/mixin.scss";

.com-whoweare {
  @include desktop {
    padding-left: 5vw;
    .com-strength .wrapper .title {
      font-size: 2rem;
    }
  }
  .com-card {
    @include desktop {
      .title:nth-child(odd) {
        height: 4rem;
      }
    }

    img {
      max-height: unset;
      min-height: unset;
      margin-bottom: unset;
      margin-bottom: 1rem;
    }
  }
  .wrapper-btnOffer {
    position: relative;
    @include desktop {
      margin-bottom: 2rem;
    }
    .btn-offer-desk {
      position: relative;
      margin: auto;
      top: 0;
    }
  }
}
