@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-brand {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  padding: 2rem 0;
  .title {
    font-size: 2rem;
    font-weight: 800;
    text-transform: uppercase;
    margin: 0 $pdMobile;
    margin-bottom: 2rem;
    @include textGlitch("titleBrand", 17, black, rgba(0, 0, 0, 0.001), rgba(255, 0, 0, 0.3), rgba(0, 0, 255, 0.3), 400, 100);
  }
  .content {
    font-size: 1rem;
    font-style: italic;
    color: #5e5e5e;
    font-weight: 500;
  }

  .block-item {
    .item {
      min-height: 450px;
      display: grid !important;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(4, 1fr);
      grid-gap: 0.5rem;

      img {
        max-height: 105px;
        width: 70px;
        margin: auto;
        filter: grayscale(100%) opacity(80%);
      }
    }
  }
  .slick-track {
    margin-bottom: 2rem;
  }
}

@include desktop {
  .com-brand {
    font-family: "Montserrat", sans-serif;
    text-align: center;
    padding: $pdDesk;

    .title {
      font-size: 3rem;
      width: fit-content;
      margin: auto;
      margin-bottom: 1rem;
      @include textGlitch("titleBrand", 17, black, rgba(0, 0, 0, 0.001), rgba(255, 0, 0, 0.3), rgba(0, 0, 255, 0.3), 800, 100);
      @include desktop2k {
        font-size: 5rem;
        margin-bottom: 5rem;
      }
    }
    .content {
      font-size: 1rem;
    }
    .block-item {
      max-width: 70vw;
      margin: auto;
      margin-top: 3rem;
      .item {
        grid-gap: 2rem;
        min-height: 520px;
        @include desktop2k {
          grid-gap: 5rem 2rem;
        }
        img {
          max-width: 100px;
          cursor: pointer;
          transition: 0.4s all ease-in-out;
          &:hover {
            filter: unset;
          }
          @include desktop2k {
            transform: scale(2);
            height: 150px;
            object-fit: contain;
            max-height: unset;
          }
        }
      }
    }
  }
}
